import React from 'react';
import emailjs from 'emailjs-com';
import "./Contact.css";

const Contact = () => {
   
   const sendMessage = (e) => {
      e.preventDefault();

      emailjs.sendForm("set_national_gmail", 'template_vjd74wMr', e.target, 'user_n6VXM67qmVMaCewsRmJT8')
      .then((result) => {
         console.log(result.text);
      }, (error) => {
         console.log(error.text);
      }).then(() => {
         document.getElementById("inputForm").reset();
      });
   }

   return(
      <div id="contactContainer">
         <h1> Contact Us </h1>
         <form id="inputForm" onSubmit={sendMessage}>
            <input type="text" className="inputItem" placeholder="Your Name" name="from_name" required/>
            <input type="email" className="inputItem" placeholder="Your Email" name="reply_to" required/>
            <textarea placeholder="Message" className="inputItem" rows="6" name="message_html" required />
            <input type="submit" className="inputItem inputButton" value="SEND MESSAGE" />
         </form>
      </div>
   );
}

export default Contact;
