import React from 'react';
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import "./SideDrawerHeader.css";

const SideDrawerHeader = props => {
   return(
      <nav className={props.classes}>
         <ul>
            <li className="drawerLink" onClick={props.sideClickHandler}>  <HashLink className="drawerCorrectStyle" to="/#mainInfo" > Information </HashLink> </li>
            <li className="drawerLink" onClick={props.sideClickHandler}> <HashLink className="drawerCorrectStyle" to="/#mainChallenge" > Challenges </HashLink> </li>
            <li className="drawerLink" onClick={props.sideClickHandler}> <HashLink className="drawerCorrectStyle" to="/#mainSponsor" > Sponsors </HashLink> </li>
            <li className="drawerLink" onClick={props.sideClickHandler}> <HashLink className="drawerCorrectStyle" to="/#mainFAQ" > FAQ </HashLink> </li>
            <Link className="drawerCorrectStyle" to="/contact"> <li className="drawerLink" onClick={props.sideClickHandler}> Contact Us </li> </Link>
         </ul>

         <a id="drawerSignupBtn" href="https://forms.gle/Pay9N5kk6JFX1KQu6"> Sign Up </a>
      </nav>
   );
}

export default SideDrawerHeader;
