import React from "react";


import "./SponsorBox.css";

const SponsorBox = props => {
    const w = props.w;
    const h = props.h;
    const linkref = props.linkref;
    const imgsrc = props.imgsrc;

    return(
        <a href={linkref}><img src={imgsrc} alt="spongor-logo" id="sponsor-logo" style={{width: w, height: h}} /></a>
    );
}


export default SponsorBox;
