import React, { useState, useEffect } from "react";

import Info from "./info/Info";
import Challenges from "./challenges/Challenges";
import Sponsor from "./sponsor/Sponsor";
import FAQ from "./FAQ/FAQ";

import "./MainBody.css";

const MainBody = () => {
    const [colorClass, setColorClass] = useState("");
    
    useEffect(() => {
        handleScroll();
        window.addEventListener("scroll", handleScroll);
    });

    const handleScroll = () => {
        console.log(window.pageYOffset);
        const scrollPos = window.pageYOffset;

        const borders = [500, 1500, 3000, 4700];

        if(scrollPos < borders[0]) //start: black
            setColorClass("body1");
        else if(scrollPos > borders[0] && scrollPos < borders[1]) //info
            setColorClass("body2");
        else if(scrollPos > borders[1] && scrollPos < borders[2]) //challenges
            setColorClass("body3");
        else if(scrollPos > borders[2] && scrollPos < borders[3]) //sponsors
            setColorClass("body4");
        else if (scrollPos > borders[3]) //faq
            setColorClass("body5");
    }

    return(
        <div id="main-body" className={colorClass}>
            <Info />
            <Challenges />
            <Sponsor />
            <FAQ />
        </div>
    );
}

export default MainBody;
