import React, { useState } from 'react';
import Header from "./header/Header";
import SideDrawerHeader from "./sideDrawerHeader/SideDrawerHeader";
import Backdrop from "./sideDrawerHeader/Backdrop";

const RespHeader = () => {
   const [SideDrawerOpen, setSideDrawerOpen] = useState(false); 

   const handleDrawerToggle = () => {
      setSideDrawerOpen(!SideDrawerOpen);
   }

   let back;
   let drawerClass;
   if(SideDrawerOpen){
      back = <Backdrop backdropClickHandler={handleDrawerToggle} />;
      drawerClass = "sideDrawer";
   } else {
      drawerClass = "sideDrawer drawerClose";
   }

   return (
      <div className="headerFont">
         <Header drawerClickHandler={handleDrawerToggle} />
         <SideDrawerHeader sideClickHandler={handleDrawerToggle} classes={drawerClass}/>
         { back }
      </div>
   );
}

export default RespHeader;
