import React from 'react';
import './Spincon.css';

const Spincon = props => {
   const spinClass = props.spinClass;
   const img = props.img;
   const reverseText = props.reverseText;
   
   
   const CWSpinner = (
         <div className={"flip-icon " + spinClass}
            animate={{y: 0}}>
            <div className={"flip-icon-inner"}>
               <div className="flip-icon-front">
                  <img src={img} alt="icon"className={spinClass + " flip-img"} />
               </div>
               <div className={"flip-icon-back"}>
                  <p className="rev-text"> {reverseText} </p>
               </div>
            </div>
         </div>
   );
   
   const CCWSpinner = (
      <div className={"flip-icon " + spinClass}>
         <div className="flip-icon-inner-rev">
            <div className="flip-icon-front">
               <img src={img} alt="icon"className={spinClass + " flip-img"} />
            </div>
            <div className={"flip-icon-back-rev"}>
               <p className="rev-text"> {reverseText} </p>
            </div>
         </div>
      </div>
   );

   const directionalSpinner = props.spin==="true"? CWSpinner:CCWSpinner;

   return directionalSpinner;
}

export default Spincon;
