import React from 'react';
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";
import logo from "../../../assets/Hackathon_logo_dev_1.png";
import { ReactComponent as Hamburger } from "../../../assets/burger.svg";
import "./Header.css";

const Header = props => {
   const topOfPage = () => {
      window.scroll(0, 0);
   }

   return (
      <header>
         <Hamburger id="burger" onClick={props.drawerClickHandler}/> 
         <Link to="/" className="headerItems" id="logo" onClick={topOfPage}> <img src={logo} alt="logo" id="headerLogo" /> </Link>

         <nav>
            <ul className="headerItems" id="linkOptions">
               <li className="listItem"> <HashLink to="/#mainInfo" className="linkStyle correctStyle"> Information </HashLink> </li>
               <li className="listItem"> <HashLink to="/#mainChallenge" className="linkStyle correctStyle"> Challenges </HashLink> </li>
               <li className="listItem"> <HashLink to="/#mainSponsor" className="linkStyle correctStyle"> Sponsors </HashLink> </li>
               <li className="listItem"> <HashLink to="/#mainFAQ" className="linkStyle correctStyle"> FAQ </HashLink> </li>
               <HashLink className="correctStyle contactLink" to="/contact"> <li className="contactItem"> Contact Us </li> </HashLink>
            </ul>
         </nav>
         {/* currently the link points to www.google.com */}
         <a id="signupBtn" href="https://forms.gle/Pay9N5kk6JFX1KQu6"> Sign Up </a> 
      </header>
   );
}

export default Header;
